	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2013/2/korean-lawmaker-who-exposed-samsungs-corruption-forced-from-office/index.md absPath of file >>> MarkdownRemark","html":"<blockquote>\n<p>Roh, who was a lawmaker for the opposition Progressive Justice Party, has been a vocal critic of Samsung, South Korea’s most powerful conglomerate, which dominates the country’s economy. In testimony to the National Assembly in 2005, he used the wiretapped conversations to call for an investigation into Samsung’s relationships with prosecutors. The probe led to the resignation of a vice justice minister but prosecutors only indicated Roh and a journalist for releasing the wiretaps.</p>\n<p>Usually South Korean lawmakers are protected by an immunity that allows them to speak freely in the National Assembly without being sued for libel or prosecuted for other charges. At issue was whether such immunity applied to the lawmaker’s actions in cyberspace. South Korea’s Supreme Court ruled that it did not.</p>\n</blockquote>\n<p>And yet Samsung is a much better company than “evil” Apple right?</p>","frontmatter":{"title":"Korean lawmaker who exposed Samsung's corruption forced from office","date":"February 15, 2013","passthroughUrl":"http://news.yahoo.com/skorean-lawmaker-loses-seat-over-samsung-wiretaps-090038078--finance.html"}}},"pathContext":{"slug":"/blog/2013/2/korean-lawmaker-who-exposed-samsungs-corruption-forced-from-office/","previous":{"fields":{"slug":"/blog/2013/2/designing-the-packaging-free-future/"},"frontmatter":{"title":"Designing the packaging-free future","passthroughUrl":"http://www.wired.com/design/2013/02/packages-without-packaging/all","type":null}},"next":{"fields":{"slug":"/blog/tesla-vs-nytimes-topgear/"},"frontmatter":{"title":"Tesla vs. NY Times and Top Gear","passthroughUrl":null,"type":null}}}}

